.accordion-item {
	padding-bottom: 40px;

	@include max-w(767) {
		padding-bottom: 20px;
	}

	&__title {
		padding-right: 60px;
		cursor: pointer;
		position: relative;

		@include max-w(575) {
			padding-right: 40px;
		}
	}

	&__icon {
		position: absolute;
		top: 5px;
		right: 20px;
		width: 10px;
		height: 10px;

		@include max-w(575) {
			right: 10px;
		}

		&:after, &:before {
			content: '';
			display: block;
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			background-color: $secondary;
		}

		&:after {
			width: 2px;
			height: 10px;
			opacity: 1;
			transition: 0.3s;
		}

		&:before {
			width: 10px;
			height: 2px;
		}
	}

	&__text {
		padding: 10px 30px 0 30px;

		@include max-w(767) {
			padding: 10px 15px 0 15px;
		}
	}
}

.accordion-block {
	.open {
		.accordion-item__icon {
			&:after {
				opacity: 0;
			}
		}
	}
}