.product-overview-main-photo {
	min-height: 495px;
	max-height: 600px;
	width: 100%;

	@include max-w(380) {
		min-height: 400px;
	}

	a {
		display: inline-block;
	}
}

.product-overview-wrap {
	padding-right: 50px;

	@include max-w(767) {
		padding-right: 0;
	}
}

.product-overview {
	position: relative;
	max-width: 340px;
	transition: 0.3s;

	@include max-w(767) {
		margin: 0 auto;
	}

	.slider-arrows {
		position: absolute;
		top: 0;
		right: 0;
		margin-bottom: 0;
		z-index: 10;
	}
}

.product-overview-nav-wrap {
	position: absolute;
	bottom: 0;
	left: 0;
	z-index: 10;
	width: 40px;
	height: auto;

	.slick-track, .slick-list {
		height: 100% !important;
	}

	.slick-track {
		.product-overview-nav-photo {
			&:first-child {
				border-top: none;
			}
		}
	}
}

.product-overview-nav-photo {
	width: 40px;
	height: 40px;
	cursor: pointer;
	transition: opacity 0.3s;

	&:hover {
		opacity: 0.7;
	}

	&.slick-slide {
		border: 0;
	}

	&.slick-slide {
		border-top: 1px solid rgba($secondary, 0.1);
	}
}

.product-info-top-line {
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	align-items: baseline;

	&__title {
		margin-bottom: 0;
		margin-right: 30px;

		&.-lg-mb-init {
			@include max-w(767) {
				margin-bottom: 15px;
			}
		}
	}

	.stars-rating {
		position: relative;
		bottom: 3px;
	}
}

.radio-list, .checkbox-list {
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	flex-wrap: wrap;

	&.-with-title {
		flex-wrap: nowrap;
	}

	&__block {
		display: -webkit-flex;
		display: -moz-flex;
		display: -ms-flex;
		display: -o-flex;
		display: flex;
		flex-wrap: wrap;

		.color-radio, .color-checkbox {
			&:last-child {
				margin-right: 0;
			}
		}
	}

	.color-radio, .color-checkbox {
		&:last-child {
			margin-right: 0;
		}
	}

	&.-vertical {
		flex-wrap: wrap;
		align-items: flex-start;

		.checkbox-list__item-wrap {
			width: 50%;
			flex-basis: 50%;
		}

		.radio-item, .checkbox-item {
			margin-bottom: 5px;
			margin-right: 0;
		}
	}

	&__title {
		margin-right: 10px;
		margin-bottom: 15px;
		font-weight: 400;
	}

	&__link {
		display: inline-block;
		margin-bottom: 15px;
	}
}

.quantity-block {
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	align-items: center;

	&__title {
		margin-right: 8px;
		margin-bottom: 0;
		font-weight: 400;
	}
}

.product-btn-wrap {
	font-size: 0;

	a {
		margin-right: 1px;
		vertical-align: top;
	}
}

.modal-product {
	.product-overview-wrap,
	.product-overview,
	.-slider-init,
	.slick-list,
	.slick-track,
	.product-overview-main-photo {
		height: 100%;
	}

	.product-overview {
		opacity: 0;

		&.-show-modal {
			opacity: 1;
		}
	}

	.product-info-wrap {
		padding: 30px 30px 30px 0;

		@include max-w(767) {
			padding: 30px 15px;
		}
	}

	@include max-w(991) {
		padding: 40px;
		margin-top: 0;
		margin-bottom: 0;
	}

	@include max-w(767) {
		padding: 30px;
	}

	@include max-w(575) {
		padding: 15px 0;
	}

	.container {
		@include max-w(767) {
			max-width: 100%;
		}
	}

	.modal-content {
		@include max-w(767) {
			padding-top: 50px;
		}
	}
}