.quantity-item {
	position: relative;
	display: inline-block;
	width: 70px;

	&__btn {
		width: 20px;
		height: 20px;
		position: absolute;
		top: 0;
		bottom: 0;
		background-color: #e5e5e5;
		text-align: center;
		line-height: 20px;
		cursor: pointer;
		transition: 0.3s;

		&:hover {
			background-color: darken(#e5e5e5, 10%);
		}

		&.-plus {
			left: 0;
		}

		&.-minus {
			right: 0;
		}
	}

	&__input {
		border: 1px solid #e5e5e5;
		text-align: center;
		height: 21px;
		padding-left: 20px;
		padding-right: 20px;
		background-color: $white;
	}
}