.bg-grid {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	z-index: 1;
	height: 100vh;

	div {
		height: 100%;
	}

	.row > div {
		&:after {
			content: '';
			display: block;
			position: absolute;
			top: 0;
			bottom: 0;
			right: 0;
			height: 0;
			width: 1px;
			background-color: rgba($black, 0.15);
			transition-property: height;
		}

		&:first-child {
			&:before {
				content: '';
				display: block;
				position: absolute;
				top: 0;
				bottom: 0;
				left: 0;
				height: 0;
				width: 1px;
				background-color: rgba($black, 0.15);
				transition-property: height;
			}
		}
	}

	&.-show-grid {
		.row > div {
			&:after, &:before {
				height: 100%;
			}

			&:nth-child(1) {
				&:before {
					transition-duration: 2s;
					transition-timing-function: ease;
					transition-delay: 0.2s;
				}
			}

			&:nth-child(1) {
				&:after {
					transition-duration: 1.5s;
					transition-timing-function: ease;
					transition-delay: 0.5s;
				}
			}

			&:nth-child(2) {
				&:after {
					transition-duration: 2s;
					transition-timing-function: ease;
					transition-delay: 0.4s;
				}
			}

			&:nth-child(3) {
				&:after {
					transition-duration: 1.5s;
					transition-timing-function: ease;
					transition-delay: 0.5s;
				}
			}

			&:nth-child(4) {
				&:after {
					transition-duration: 2.5s;
					transition-timing-function: ease;
					transition-delay: 0s;
				}
			}

			&:nth-child(5) {
				&:after {
					transition-duration: 2s;
					transition-timing-function: ease;
					transition-delay: 0s;
				}
			}

			&:nth-child(6) {
				&:after {
					transition-duration: 1.5s;
					transition-timing-function: ease;
					transition-delay: 0.7s;
				}
			}
		}
	}
}