.checkout-steps {
	&__title {
		display: none;
	}

	&__section {
		padding-bottom: 50px;

		@include max-w(767) {
			padding-bottom: 30px;
		}
	}

	&__left-side {
		margin-bottom: 25px;
	}

	.steps {
		margin-bottom: 40px;

		@include max-w(767) {
			margin-bottom: 25px;
		}

		@include max-w(575) {
			margin-bottom: 15px;
		}

		li {
			display: inline-block;
			padding-right: 30px;

			&:last-child {
				padding-right: 0;
			}

			&.current {
				a {
					opacity: 1;

					&:hover {
						color: $body-color;
					}
				}
			}
		}

		a {
			font-family: $headings-font-family;
			font-size: $h4-font-size;
			font-weight: 600;
			opacity: 0.5;

			@include max-w(991) {
				font-size: 1.3rem;
			}

			@include max-w(575) {
				font-size: 1.2rem;
			}
		}
	}

	.current-info {
		display: none;
	}

	.actions > ul {
		display: -webkit-flex;
		display: -moz-flex;
		display: -ms-flex;
		display: -o-flex;
		display: flex;
		align-items: center;
		justify-content: space-between;
	}
}