.section {
	padding: 30px 0;

	@include max-w(767) {
		padding: 20px 0;
	}
}

.section {
	&-welcome {
		&.section {
			padding: 0;
		}
	}

	&-our-story {
		&__text {
			padding-bottom: 320px;

			@include max-w(991) {
				padding-bottom: 0;
			}
		}

		&__photo {
			@include max-w(575) {
				max-width: 300px;
			}
		}
	}

	&-advantages-our-shop {
		margin-top: -350px;

		@include max-w(991) {
			margin-top: 0;
		}
	}

	&-product-info-single {
		padding-top: 0;

		@include max-w(767) {
			padding-top: 20px;
		}
	}

	&-reviews {
		padding-top: 0;
	}

	&-review-form {
		padding-bottom: 0;
	}

	&-main-contacts {
		overflow: hidden;

		.contacts-main-content {
			padding-bottom: 70px;

			@include max-w(575) {
				padding-bottom: 40px;
			}
		}
	}

	&-related-articles {
		margin-top: -50px;
	}

	&-related-articles {
		margin-top: -90px;

		@include max-w(991) {
			margin-top: -70px;
		}

		@include max-w(767) {
			margin-top: 0;
		}
	}

	&-payment {
		padding-top: 0;
	}

	&-shopping-cart {
		&__total-price {
			@include max-w(767) {
				padding-top: 20px;
			}
		}
	}

	&-checkout {
		padding-top: 0;
	}
}

.article {
	&-news-single {
		padding-top: 0;
	}
}