.filter-labels-list {
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	flex-wrap: wrap;
	margin-bottom: 15px;
}

.filter-label-link {
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	align-items: center;
	margin-right: 30px;

	&__title {
		margin-right: 10px;
		opacity: 0.5;
	}

	&__value {
		margin-right: 10px;
		opacity: 0.5;

		&.-color {
			display: inline-block;
			width: 40px;
			height: 20px;

			&.-blue {
				background-color: rgba($blue, 0.5);
			}
		}
	}

	&__close {
		opacity: 0.5;

		&:hover {
			opacity: 1;
		}
	}
}