.slider-dots-style {
	.slick-dots {
		padding: 30px 0 50px 0;
		line-height: 0;
		li {
			display: inline-block;
			line-height: 0;
			margin-left: 10px;
			button {
				width: 10px;
				height: 10px;
				padding: 0;
				display: inline-block;
				font-size: 0;
				background-color: $body-color;
				border: none;
				border-radius: 50%;
				cursor: pointer;
				position: relative;
			}

			&.slick-active {
				button {
					&:before {
						position: absolute;
						top: 2px;
						left: 2px;
						content: "";
						display: inline-block;
						width: 6px;
						height: 6px;
						border-radius: 50%;
						background-color: $white;
					}
				}
			}
		}
	}
}

@media only screen and (max-width: 991px) {
	.slider-dots-style {
		.slick-dots {
			padding-bottom: 30px;
		}
	}
}

@media only screen and (max-width: 575px) {
	.slider-dots-style {
		.slick-dots {
			padding-top: 20px;
			padding-bottom: 20px;
		}
	}
}