/* FORMS ============================ */

.main-form {
	width: 100%;
}

.form-control {
	height: 40px;
	border-bottom: 2px solid $secondary;

	@include max-w(575) {
		height: 35px;
	}

	label {
		cursor: text;
	}
}

textarea.form-control {
	height: 130px;
	resize: none;
	padding-top: 15px;
	display: block;
}

input[type="text"],
input[type="password"],
input[type="tel"],
input[type="email"],
textarea.form-control {
	width: 100%;
	border-radius: 0;
}

input:focus+label,
textarea:focus+label,
input.active+label,
textarea.active+label {
	top: -8px;
	font-size: 0.7rem;
}

.form-group {
	position: relative;

	label {
		position: absolute;
		left: 10px;
		top: 8px;
		cursor: text;
		transition: 0.5s;
		font-weight: 400;
		margin-bottom: 0;
	}

	&.active {
		label {
			top: -8px;
			font-size: 0.7rem;
		}

		input, textarea {
			background-color: transparent;
		}

		&.textarea-triangle {
			&:after {
				@include arr(10, 10, $secondary, br);
			}
		}
	}

	// error label
	> label.error {
		color: $red;
		top: 100%;
		left: auto;
		right: 0;
		font-size: 0.75rem;
		transition: 0s;
	}

	// error input
	> input.error {
		border-bottom-color: $red;
		color: $body-color;
		font-size: $font-size-base;
	}

	// select error
	> select.error {
		+ .select2-container--custom {
			border-bottom-color: $red;
		}
	}
}

// BOTTOM BAR LOADING

.bar {
	position: relative;
	top: 1px;
	display: block;
	max-width: 100%;
	width: 100%;
	&:before {
		content: '';
		height: 2px;
		width: 0;
		bottom: 1px;
		position: absolute;
		background-color: $white;
		transition: width 1s ease;
	}
	&:before {
		left: 0%;
	}
}

input:focus~.bar:before,
textarea:focus~.bar:before {
	width: 100%;
}

// BOTTOM BAR LOADING END

// BOTTOM TRIANGLE ANIMATION

.textarea-triangle {
	position: relative;

	&:after {
		content: '';
		@include arr(10, 10, $secondary, br);
		display: inline-block;
		position: absolute;
		bottom: 0;
		right: 0;
		@include tr(0.3s);
	}
}

.triangle-fill {
	&:after {
		@include tr(0.3s);
		@include arr(10, 10, $secondary, br);
	}
}

// BOTTOM TRIANGLE ANIMATION

.form-label-help {
	padding-top: 5px;
}

// SUBSCRIBE BLOCK
.subscribe-block {
	margin-bottom: 20px;
	.card-title {
		margin-bottom: 15px;
	}
}

.subscribe-form {
	position: relative;
	input[type="email"] {
		padding-right: 45px;
	}
	.submit-icon {
		position: absolute;
		top: 0;
		bottom: 0;
		right: 10px;
		width: 25px;
		height: 100%;
		padding: 0;
		background-color: transparent;
		border: none;
		margin-bottom: 0;
		display: -webkit-flex;
		display: -moz-flex;
		display: -ms-flex;
		display: -o-flex;
		display: flex;
		align-items: center;
		justify-content: flex-end;
		z-index: 5;
		cursor: pointer;

		&:hover {
			span {
				color: lighten($secondary, 25%);
				transition: color 0.3s ease;
			}
		}

		span {
			font-size: 1.25rem;
			color: $body-color;
			transition: color 0.3s ease;
		}
	}
}

// radio/checkbox
.color-radio, .color-checkbox {
	display: inline-block;
	margin-right: 10px;
	margin-bottom: 15px;
	height: 20px;
	cursor: pointer;
	user-select: none;
	position: relative;

	&-checkmark {
		display: inline-block;
		width: 40px;
		height: 20px;

		&:after {
			content: '';
			display: inline-block;
			width: 100%;
			height: 1px;
			background-color: transparent;
			position: absolute;
			bottom: -6px;
			left: 0;
			right: 0;
			transition: 0.3s;
		}

		&.-red {
			background-color: $red;
		}

		&.-white {
			background-color: $white;
			border: 1px solid #E7E7E7;
		}

		&.-black {
			background-color: $body-color;
		}

		&.-blue {
			background-color: $blue;
		}

		&.-teal {
			background-color: $teal;
		}

		&.-brown {
			background-color: $brown;
		}
	}

	&-input {
		position: absolute;
		top: 0;
		left: 0;
		z-index: -1;
		visibility: hidden;

		// checked styles
		&:checked + {
			.color-radio-checkmark, .color-checkbox-checkmark {
				&:after {
					background-color: $body-color;
				}
			}
		}
	}
}

// radio/checkbox
.radio-item, .checkbox-item {
	display: inline-block;
	margin-right: 20px;
	margin-bottom: 15px;
	cursor: pointer;
	user-select: none;
	position: relative;

	&-checkmark {
		display: inline-block;

		&:after {
			content: '';
			display: inline-block;
			width: 100%;
			height: 1px;
			background-color: transparent;
			position: absolute;
			bottom: 0;
			left: 0;
			right: 0;
			transition: 0.3s;
		}
	}

	&-input {
		position: absolute;
		top: 0;
		left: 0;
		z-index: -1;
		visibility: hidden;

		// checked styles
		&:checked + {
			.radio-item-checkmark, .checkbox-item-checkmark {
				&:after {
					background-color: $body-color;
				}
			}
		}
	}
}

// custom checkbox

$checkbox-bg-color: $white;
$checkbox-active-bg-color: $white;

.custom-checkbox {
	position: relative;
	display: inline-block;
	padding-left: 25px;
	line-height: 20px;
	cursor: pointer;
	user-select: none;
	margin-bottom: 0;

	&__checkmark {
		position: absolute;
		top: 50%;
		left: 0;
		transform: translate(0, -50%);
		width: 20px;
		height: 20px;
		background-color: $checkbox-bg-color;
		transition: 0.3s;
		border: 2px solid $secondary;
	}

	&__icon {
		position: absolute;
		top: 2px;
		left: 1px;
		display: inline-block;
		font-size: 0.75rem;
		color: $secondary;
		opacity: 0;
		transition: 0.2s;
	}

	&__input {
		position: absolute;
		top: 0;
		left: 0;
		z-index: -1;
		visibility: hidden;

		// checked styles
		&:checked + {
			.custom-checkbox__checkmark {
				background-color: $checkbox-active-bg-color;

				.custom-checkbox__icon {
					opacity: 1;
				}
			}
		}

		// disabled styles
		&:disabled + {
			.custom-checkbox__checkmark {
				background-color: rgba($checkbox-bg-color, 0.5);
			}
		}
	}

	&:hover {
		.custom-checkbox__input:not(:disabled):not(:checked) + {
			.custom-checkbox__checkmark {
				background-color: lighten($checkbox-bg-color, 10%);
			}
		}
	}

}

.register-sign-in-form {
	.-last-input {
		margin-bottom: 5px;
	}
}

.form-group {
	.show-password-text-btn {
		display: inline-block;
		position: absolute;
		top: 50%;
		right: 10px;
		transform: translate(0, -50%);
		font-size: 20px;
		cursor: pointer;

		span {
			transition: 0.3s;
		}

		&:hover {
			span {
				color: $secondary;
			}
		}
	}

	&.-max-w-340 {
		max-width: 340px;
		padding-right: 30px;

		@include max-w(767) {
			padding-right: 15px;
		}

		@include max-w(575) {
			max-width: 100%;
			padding-right: 0;
		}
	}
}

.search-input-block {
	margin-bottom: 10px;
	position: relative;

	&__btn {
		position: absolute;
		top: 50%;
		right: 20px;
		transform: translate(0, -50%);
		background-color: transparent;
		font-size: 60px;
		border: none;
		color: rgba($white, 0.8);
		cursor: pointer;
		padding: 0;
		line-height: 1;
		transition: 0.3s;

		@include max-w(991) {
			font-size: 45px;
		}

		@include max-w(767) {
			font-size: 35px;
		}

		@include max-w(575) {
			font-size: 30px;
			right: 10px;
		}

		&:hover {
			color: rgba($white, 1);
		}
	}
}

.search-input {
	padding: 5px 0;
	padding-right: 100px;
	border: none;
	border-bottom: 4px solid rgba($white, 0.8);
	font-family: "Barlow Condensed", sans-serif;
	font-size: 4.5rem;
	line-height: 1;
	font-weight: 700;
	color: $white;
	@include placeholder-color(rgba($white, 0.8));
	@include placeholder-opacity(0.5s);
	background-color: transparent;

	@include max-w(991) {
		font-size: 3.8rem;
		padding-right: 85px;
	}

	@include max-w(767) {
		font-size: 3rem;
		padding-right: 75px;
	}

	@include max-w(575) {
		font-size: 2.3rem;
		padding-right: 50px;
	}
}


// FILTER INPUTS

.input-group-sm {
	.form-control {
		height: 30px;
	}
}

.form-control {
	&.-no-bottom-border {
		border-bottom: none;
	}

	&.-no-active-bg {
		&:focus {
			background-color: $gray-100;
		}
	}
}

.range-filter-form {
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	align-items: center;
	margin-bottom: 20px;

	&__divider {
		display: inline-block;
		width: 10px;
		height: 1px;
		background-color: $secondary;
		margin: 0 12px;
	}

	.input-group-sm {
		width: 80px;
	}
}

.left-side-form, .right-side-form {
	height: calc(100% - 5px);
}

.right-side-form {
	.form-group {
		height: calc(100% - 1.25rem);

		.form-control {
			height: 100%;
		}
	}
}

.input-with-title {
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	align-items: center;

	@include max-w(767) {
		display: block;
		margin-bottom: 10px;
	}

	&__text {
		margin-right: 20px;
		margin-bottom: 0;
		margin-top: 5px;
		white-space: nowrap;
	}

	&__input {
		width: 100%;
		margin-bottom: 0;
	}
}

// CUSTOM SELECT
.select2-container {
	// custom theme
	&--custom {
		display: block;
		background-color: $gray-100;
		height: 40px;
		border-bottom: 2px solid $secondary;

		&:focus {
			outline: none;
		}

		.selection {
			display: block;
			height: 100%;

			&:focus {
				outline: none;
			}
		}

		.select2-selection--single {
			height: 100%;

			&:focus {
				outline: none;
			}

			.select2-selection__rendered {
				padding: $input-btn-padding-y $input-btn-padding-x;
				height: 100%;
			}
		}

		.select2-selection__arrow {
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			width: 30px;
			z-index: 1;

			b {
				display: inline-block;
				width: 100%;
				height: 100%;

				&:before {
					position: absolute;
					top: 50%;
					left: 50%;
					content: "\e911";
					display: inline-block;
					font-family: icomoon !important;
					font-size: 0.75rem;
					line-height: 1;
					transition: 0.3s;
					transform: translate(-50%, -50%) rotate(0);
				}
			}
		}

		&.select2-container--open {
			.select2-selection__arrow {
				b {
					&:before {
						transform: translate(-50%, -50%) rotate(180deg);
					}
				}
			}
		}

		.select2-dropdown {
			border-color: rgba($secondary, 0.5);
			border-radius: 0;
			max-height: 152px;
			overflow: auto;

			&.select2-dropdown--above {
				border-bottom: 1px solid rgba($secondary, 0.5);
			}

			.select2-results__option {
				height: 38px;
				padding: $input-btn-padding-y $input-btn-padding-x;
				transition: 0.3s;

				&:hover {
					background-color: rgba($secondary, 1);
					color: $white;
				}
			}
		}
	}
}

// CUSTOM ROUND RADIO

$radio-checkmark-size: 14px;
$radio-bg-color: $secondary;

.simple-circle-radio {
	position: relative;
	display: inline-block;
	padding-left: 25px;
	margin-right: 10px;
	margin-bottom: 1rem;
	line-height: 14px;
	cursor: pointer;
	user-select: none;

	&__checkmark {
		position: absolute;
		top: 50%;
		left: 0;
		transform: translate(0, -50%);
		width: $radio-checkmark-size;
		height: $radio-checkmark-size;
		border-radius: 50%;
		border: 1px solid rgba($radio-bg-color, 0.5);
		transition: 0.3s;
	}

	&__icon {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		display: inline-block;
		width: 8px;
		height: 8px;
		background-color: $radio-bg-color;
		border-radius: 50%;
		transition: 0.2s;
		opacity: 0;
	}

	&__input {
		position: absolute;
		top: 0;
		left: 0;
		z-index: -1;
		visibility: hidden;

		// checked styles
		&:checked + {
			.simple-circle-radio__checkmark {
				.simple-circle-radio__icon {
					opacity: 1;
				}
			}
		}

		// disabled styles
		&:disabled + {
			.simple-circle-radio__checkmark {
				border-color: rgba($radio-bg-color, 0.2);

				.simple-circle-radio__icon {
					opacity: 1;
					background-color: rgba($radio-bg-color, 0.2);
				}
			}

		}
	}
}