.tabs-navigation {
	margin-bottom: 40px;

	@include max-w(767) {
		margin-bottom: 15px;
	}

	.tab-nav-item {
		position: relative;

		@include max-w(575) {
			margin-right: 15px;
		}

		&:after {
			content: '';
			display: inline-block;
			width: 100%;
			height: 1px;
			background-color: transparent;
			position: absolute;
			bottom: 0;
			left: 0;
			right: 0;
			transition: 0.3s;
		}

		&.active {
			&:after {
				background-color: $body-color;
			}
		}
	}
}