.welcome-banner {
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
	min-height: 500px;
	width: 83.333333%;
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	align-items: center;
	position: relative;

	&__text-block {
		margin-left: 80%;
	}

	&__sale-number {
		font-family: 'Barlow Condensed', sans-serif;
		font-weight: 700;
		font-size: 12.5rem;
		color: #fefefe;
		position: absolute;
		top: -70px;
		left: -10px;
		line-height: 1;
	}
}

@media only screen and (max-width: 991px) {
	.welcome-banner {
		min-height: 350px;

		&__sale-number {
			font-size: 10rem;
			top: -55px;
		}
	}
}

@media only screen and (max-width: 767px) {
	.welcome-banner {
		min-height: 300px;

		&__sale-number {
			font-size: 8rem;
			top: -45px;
		}
	}
}

@media only screen and (max-width: 575px) {
	.welcome-banner {
		min-height: 250px;
		width: 100%;
		align-items: flex-end;

		&__sale-number {
			font-size: 6rem;
			top: -35px;
		}

		&__text-block {
			margin-left: auto;
			text-align: right;
			padding-bottom: 15px;
		}
	}
}

.welcome-slider {
	.slick-slide {
		overflow: hidden;
		display: -webkit-flex;
		display: -moz-flex;
		display: -ms-flex;
		display: -o-flex;
		display: flex;
	}
}