.side-cart {
	width: 295px;
	overflow-x: auto;
	background-color: $white;
	border-left: 1px solid rgba($black, 0.1);
	position: fixed;
	top: 0;
	right: 0;
	bottom: 0;
	z-index: 1100;
	transition: 0.5s;
	transform: translate(100%, 0);
	padding: 30px 10px;
	padding-top: 80px;

	@include max-w(767) {
		padding-top: 65px;
	}

	@include max-w(575) {
		padding-top: 55px;
		width: 270px;
	}

	&__price-block {
		text-align: right;
		padding: 20px 0;

		.card-title {
			margin-bottom: 5px;
		}

		p {
			margin-bottom: 0;
		}
	}

	&__buttons {
		display: -webkit-flex;
		display: -moz-flex;
		display: -ms-flex;
		display: -o-flex;
		display: flex;

		.btn {
			min-width: auto;
			width: 100%;

			&:first-child {
				margin-right: 1px;
			}
		}
	}
}

body.-open-side-cart {
	overflow: hidden;

	.side-cart {
		transform: translate(0, 0);
	}
}

.custom-overlay {
	position: fixed;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
	width: 100%;
	height: 100%;
	background-color: $modal-backdrop-bg;
	z-index: 1099;
	transition: 0.5s;
	opacity: 0;

	&.-show {
		opacity: 1;
	}
}