.labels-wrap {
	position: absolute;
	top: 0;
	left: 0;
	z-index: 10;
}

// PRODUCT CARD
.product-card {
	max-width: 180px;
	padding-right: 15px;
	margin-bottom: 20px;

	@include max-w(767) {
		padding-right: 20px;
	}

	&__photo {
		position: relative;
		overflow: hidden;
		height: 0;
		padding-bottom: 145.6%;
		margin-bottom: 5px;

		img {
			position: absolute;
			top: 0;
			right: 0;
			left: 0;
			bottom: 0;
			width: 100%;
			height: 100%;
		}

		.product-card__hover-buttons {
			.btn-square {
				transition-duration: 0.5s, 0.5s;
				transition-timing-function: ease;
				transition-property: opacity, background-color;

				@for $i from 0 to 10 {
					$speed : 0.1s;
					$step : 0.1s * $i;

					$speed : $speed + $step;

					&:nth-child(#{$i + 1}) {
						transition-delay: $speed, 0s;
					}
				}
			}
		}

		&:hover {
			.product-card__hover-buttons {
				.btn-square {
					opacity: 1;
				}
			}
		}
	}

	&__hover-buttons {
		position: absolute;
		bottom: 0;
		right: 0;

		.btn-square {
			opacity: 0;
		}
	}

	.card-title {
		display: inline-block;
		margin-bottom: 5px;
	}
}

// NEWS CARD

.photo-with-date {
	position: relative;
	padding-right: 20px;

	&__text {
		font-weight: 400;
		font-size: 0.625rem;
		color: #999999;
		writing-mode: vertical-rl;
		transform: rotate(180deg);
		position: absolute;
		bottom: 0;
		right: 3px;
	}

	&__photo {
		display: inline-block;
	}
}

.photo-with-date-wrap {
	padding-right: 20px;
	max-width: 350px;
	margin-bottom: 30px;

	@include max-w(575) {
		margin-bottom: 15px;
	}
}

.news-card {
	padding-right: 20px;
	max-width: 350px;
	margin-bottom: 30px;

	@include max-w(575) {
		margin-bottom: 15px;
	}

	&__photo-wrap {
		margin-bottom: 5px;
	}

	.card-title {
		margin-bottom: 10px;
	}

	&__text {
		margin-bottom: 10px;
	}
}

.advantages-our-shop-item {
	margin-bottom: 20px;

	&__number {
		margin-bottom: 10px;
	}
}

.testimonials-item {
	max-width: 290px;
	padding-right: 30px;
	margin-bottom: 30px;

	@include max-w(575) {
		max-width: 100%;
		padding-right: 0;
	}

	&__photo-social {
		display: -webkit-flex;
		display: -moz-flex;
		display: -ms-flex;
		display: -o-flex;
		display: flex;
		align-items: flex-end;
		margin-bottom: 15px;
	}

	.small-photo {
		margin-right: 10px;
	}

	.social-icons {
		margin-bottom: 0;
	}
}

.small-photo {
	min-width: 70px;
	max-width: 70px;
}

.review-item {
	margin-bottom: 50px;

	@include max-w(767) {
		margin-bottom: 30px;
	}

	&__autor {
		display: -webkit-flex;
		display: -moz-flex;
		display: -ms-flex;
		display: -o-flex;
		display: flex;
		margin-bottom: 15px;

		@include max-w(767) {
			flex-direction: column;
		}
	}

	&__photo {
		margin-right: 10px;

		@include max-w(767) {
			order: 2;
		}
	}

	&__name {
		white-space: nowrap;

		@include max-w(767) {
			order: 1;
		}
	}

	&__info-block {
		display: -webkit-flex;
		display: -moz-flex;
		display: -ms-flex;
		display: -o-flex;
		display: flex;
		justify-content: space-between;
		align-items: center;
	}

	&__info-left {
		display: -webkit-flex;
		display: -moz-flex;
		display: -ms-flex;
		display: -o-flex;
		display: flex;
		align-items: center;
	}

	&__date {
		margin-right: 50px;
		margin-bottom: 0;

		@include max-w(767) {
			margin-right: 30px;
		}
	}

	&__reply-link {
		margin-bottom: 0px;
	}
}

.navigation-block {
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	align-items: center;
	justify-content: space-between;

	@include max-w(575) {
		padding-top: 20px;
	}
}