.pagination {
	align-items: center;
	margin-bottom: 0;

	.page-item {
		padding: 0 0.625rem;

		&.active {
			.page-link {
				border-bottom: 1px solid $secondary;
			}
		}

		&:first-child, &:last-child {
			font-size: 1.2rem;
			padding: 0;
		}

		&:first-child {
			margin-right: 20px;

			@include max-w(575) {
				margin-right: 10px;
			}
		}

		&:last-child {
			margin-left: 20px;

			@include max-w(575) {
				margin-left: 10px;
			}
		}
	}
}