.contact-list {
	&__item:not(:last-child) {
		margin-bottom: 10px;
	}
}

.contact-item {
	position: relative;
	padding-left: 25px;

	&.-gray {
		opacity: 0.7;
	}

	&__icon {
		position: absolute;
		top: 3px;
		left: 0;
		display: inline-block;
		min-width: 15px;
	}
}

.contacts-list-block {
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	flex-wrap: wrap;

	@include max-w(575) {
		margin-bottom: 20px;
	}

	.contact-item-block {
		margin-bottom: 40px;
		flex-basis: 50%;
		overflow: hidden;

		@include max-w(575) {
			margin-bottom: 20px;
			flex-basis: 100%;
		}
	}

	.contact-item {
		padding-right: 15px;
	}
}