.header {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	width: 100%;
	max-height: 100%;
	z-index: 100;

	&__logo {
		margin-right: 30px;

		@include max-w(575) {
			margin-right: 15px;
		}
	}

	&__right-block {
		display: -webkit-flex;
		display: -moz-flex;
		display: -ms-flex;
		display: -o-flex;
		display: flex;
		justify-content: flex-end;
		position: relative;

		&.-toggle-on-mobile {

			@include min-w(576) {
				.navbar-additional {
					display: block !important;
				}
			}

			@include max-w(575) {
				.navbar-additional {
					position: absolute;
					top: -10px;
					right: 0;
					text-align: center;
					background-color: $secondary;
					padding-top: 50px;
					display: none;

					li {
						margin: 10px;

						a {
							color: $white;
						}
					}
				}
			}

		}
	}

	&-container {
		transition: 0.5s;
		border-bottom: 1px solid transparent;

		&.-filled {
			background-color: $white;
			border-bottom: 1px solid rgba($black, 0.1);
		}
	}

	&-menu-nav-wrap {
		flex-grow: 1;
		display: -webkit-flex;
		display: -moz-flex;
		display: -ms-flex;
		display: -o-flex;
		display: flex;
		justify-content: space-between;
		align-items: center;
	}
}

.navbar {
	min-height: 80px;

	@include max-w(767) {
		min-height: 65px;
	}

	@include max-w(575) {
		min-height: 55px;
		padding: 5px 0;
	}
}

.header-nav-menu {
	transition: 0.5s;
	align-items: center;

	@include max-w(767) {
		padding: 0.3rem 0;
	}

	.nav-item {
		margin-left: 13px;

		&:first-child {
			margin-left: 0;
		}
	}

	.nav-link {
		cursor: pointer;
		padding: 0;
		transition: 0.3s;
		display: inline-block;

		&.active {
			text-shadow: 0.3px 0 0 $body-color, -0.3px 0 0 $body-color, 0 0.3px 0 $body-color, 0 -0.3px 0 $body-color;
		}

		&:hover {
			text-shadow: 0.3px 0 0 $body-color, -0.3px 0 0 $body-color, 0 0.3px 0 $body-color, 0 -0.3px 0 $body-color;
		}
	}
}

.navbar-additional {
	margin-bottom: 0;
	line-height: 1;

	li {
		display: inline-block;
		margin-left: 20px;

		&:first-child {
			margin-left: 0;
		}
	}

	&__icon {
		display: inline-block;
		span {
			font-size: 1.2rem;
		}

		&:hover {
			text-shadow: 0.3px 0 0 $body-color, -0.3px 0 0 $body-color, 0 0.3px 0 $body-color, 0 -0.3px 0 $body-color;
		}
	}
}

@media only screen and (max-width: 575px) {
	.header-nav-menu {
		&-nav {
			display: none;
		}
	}
}