.navbar-brand {
	display: flex;
	align-items: center;
	flex-direction: column;
	color: $body-color;
	padding-left: 2px;

	@include max-w(767) {
		flex-direction: row;
	}

	.icon-logo-wrap {
		max-width: 30px;
		margin-bottom: 5px;

		@include max-w(767) {
			margin-right: 5px;
		}

		img {
			@include img-res;
		}
	}

	.logo-text {
		transition: 0.3s;
		line-height: 1;

		@include max-w(767) {
			line-height: 1.4;
		}

		@include max-w(575) {
			display: none;
		}
	}
}