.banner-title {
	font-family: $headings-font-family;
	font-size: 4.5rem;
	font-weight: 400;
	line-height: 1;
	margin-bottom: 25px;

	@include max-w(1300) {
		font-size: 4rem;
	}

	@include max-w(991) {
		font-size: 3rem;
	}

	@include max-w(767) {
		font-size: 2.5rem;
		margin-bottom: 15px;
	}

	@include max-w(575) {
		font-size: 1.8rem;
		margin-bottom: 15px;
	}

	strong {
		font-size: 9.375rem;
		line-height: 0.6;

		@include max-w(1300) {
			font-size: 8rem;
		}

		@include max-w(991) {
			font-size: 6rem;
		}

		@include max-w(767) {
			font-size: 5rem;
		}

		@include max-w(575) {
			font-size: 2.8rem;
		}
	}
}

h2 , .h2 {
	line-height: 1;
	margin-bottom: 40px;

	&.-gray {
		color: $gray;
		opacity: 0.3;
	}

	@include max-w(991) {
		font-size: 3rem;
	}

	@include max-w(767) {
		font-size: 2.5rem;
	}

	@include max-w(575) {
		font-size: 1.8rem;
		margin-bottom: 15px;
	}
}

h4, .h4 {
	font-weight: 600;
	line-height: 1.25;

	@include max-w(991) {
		font-size: 1.3rem;
	}

	@include max-w(575) {
		font-size: 1.2rem;
	}
}

h6, .h6 {
	font-family: $font-family-base;
	line-height: 1.4;
	margin-bottom: 5px;

	@include max-w(767) {
		margin-bottom: 10px;
	}
}

// EXTRA SMALL TEXT
.extra-small-text {
	font-size: 0.625rem;
	color: $gray;
}

// PRICE BLOCK
.price-block {

	&__current-price {
		margin-right: 8px;
	}

	&__old-price {
		font-size: 0.625rem;
		opacity: 0.5;
		text-decoration: line-through;
	}

	&.-lg {

		.price-block__current-price {
			font-size: 1.5rem;
			margin-right: 5px;
		}

		.price-block__old-price {
			font-size: 1rem;
		}
	}
}

.gray-text {
	color: $gray;
}

.small-text {
	font-size: 0.75rem;
}

.vertical-list {
	li {
		margin-bottom: 15px;

		&:last-child {
			margin-bottom: 0;
		}
	}

	a {
		display: inline-block;

		&:hover {
			text-shadow: 0.3px 0 0 $body-color, -0.3px 0 0 $body-color, 0 0.3px 0 $body-color, 0 -0.3px 0 $body-color;
		}
	}
}

.text-block {
	&.-with-brackets {
		p {
			position: relative;

			&:before {
				content: '«';
				display: inline-block;
				padding-right: 2px;
			}

			&:after {
				content: '»';
				display: inline-block;
				padding-left: 2px;
			}
		}
	}
}

.small-text {
	line-height: 1.7;
}