.footer {
	padding: 20px 0 15px 0;
	position: absolute;
	bottom: 0;
	left: 0;
	right: 0;
	width: 100%;

	&__copyright {
		opacity: 0.3;
		margin-bottom: 0;
	}

	&__nav-list {
		display: -webkit-flex;
		display: -moz-flex;
		display: -ms-flex;
		display: -o-flex;
		display: flex;
		flex-wrap: wrap;
		padding-top: 35px;
		li {
			flex-basis: 50%;
			margin-bottom: 10px;
			overflow: hidden;
			a {
				&:hover {
					text-shadow: 0.3px 0 0 $body-color, -0.3px 0 0 $body-color, 0 0.3px 0 $body-color, 0 -0.3px 0 $body-color;
				}
			}
		}
	}
}

.follow-block {
	.follow-text {
		color: #aaa;
		display: inline-block;
		margin-right: 10px;
		margin-bottom: 0;
	}

	.social-icons {
		margin-bottom: 0;
	}
}

@media only screen and (max-width: 575px) {
	.footer {
		&__nav-list {
			padding-top: 0;
			margin-bottom: 20px;
		}
	}
}