.cart-list-item {
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	margin-bottom: 10px;

	&__photo {
		position: relative;
		display: inline-block;
		width: 70px;
		padding-bottom: 100px;
		flex: 0 0 auto;
		max-width: none;
		opacity: 1;

		&:hover {
			opacity: 0.8;
		}
	}

	&__main-content {
		position: relative;
		width: 100%;
		padding-left: 10px;
		padding-right: 20px;

		p {
			margin-bottom: 0;
		}
	}

	&__close-round-btn {
		position: absolute;
		top: 0;
		right: 0;
	}
}

.cart-list-block {
	padding-bottom: 105px;
	position: relative;

	@include max-w(575) {
		padding-bottom: 50px;
	}

	&:after {
		content: '';
		display: inline-block;
		width: 250px;
		height: 1px;
		position: absolute;
		bottom: 0;
		right: -10px;
		background-color: #e5e5e5;
	}

	.cart-list-item {
		&:last-child {
			margin-bottom: 0;
		}
	}
}