body {
	position: relative;
}

.main-wrap {
	position: relative;
	z-index: 10;
	min-height: 100vh;
	transition: 0.5s;
}

.main-page {
	padding-top: 80px;

	@include max-w(767) {
		padding-top: 65px;
	}

	@include max-w(575) {
		padding-top: 55px;
	}
}

.content-wrap {
	padding-top: 75px;

	@include max-w(991) {
		padding-top: 50px;
	}

	@include max-w(767) {
		padding-top: 30px;
	}
}

.-mb-40 {
	margin-bottom: 40px;

	@include max-w(767) {
		margin-bottom: 15px;
	}
}

.-mb-25 {
	margin-bottom: 25px !important;
}

.-mb-20 {
	margin-bottom: 20px !important;
}

.-mb-15 {
	margin-bottom: 15px !important;
}

.-mb-10 {
	margin-bottom: 10px !important;
}

.-mb-5 {
	margin-bottom: 5px !important;
}

.-mb-0 {
	margin-bottom: 0 !important;
}

.-mt-5 {
	margin-top: 5px !important;
}

.-mt--5 {
	margin-top: -5px !important;
}

.-pr-50 {
	padding-right: 50px;

	@include max-w(991) {
		padding-right: 30px;
	}

	@include max-w(575) {
		padding-right: 0;
	}
}

.modal-dialog {
	&.-w-975 {
		max-width: 975px;
	}

	&.-w-485 {
		max-width: 485px;

		@include max-w(575) {
			max-width: 100%;
		}
	}

	&.-w-810 {
		max-width: 810px;
	}
}

blockquote {
	background-color: $gray-100;
	padding: 15px 25px;
	margin-bottom: 10px;

	@include max-w(575) {
		padding: 15px;
	}

	p {
		position: relative;

		&:first-child {
			&:before {
				content: '“';
				display: inline-block;
				position: absolute;
				top: 0;
				left: -6px;
			}
		}

		&:last-child {
			margin-bottom: 0;

			&:after {
				content: '”';
				display: inline-block;
			}
		}
	}
}