.modal-search {
	padding: 0 15px;

	@include max-w(575) {
		padding: 0;
	}

	.modal-content {
		background-color: transparent;
	}
}

.search-input-block {
	margin-bottom: 10px;
}

.modal-backdrop {
	&.-opacity-95 {
		background-color: rgba($secondary, 0.95);
	}
}

.search-healp-list {
	font-size: 1.125rem;
	font-weight: 400;

	@include max-w(575) {
		font-size: 1rem;
	}

	li {
		display: inline-block;
		margin-right: 20px;
		margin-bottom: 5px;

		@include max-w(575) {
			margin-right: 10px;
		}

		&:last-child {
			margin-right: 0;
		}
	}

	a {
		line-height: 20px;
		display: inline-block;
		border-bottom: 1px solid $white;
		color: rgba($white, 0.8);

		&:hover {
			border-bottom-color: transparent;
			color: rgba($white, 1);
		}
	}
}