.shopping-cart-table {
	&__header {
		p {
			margin-bottom: 0;
		}

		.shopping-cart-table__delete-col {
			width: 35px;
		}

		@include max-w(767) {
			display: none;
		}
	}
}

.shopping-cart-table-item {
	padding: 15px 0;
	transition: 0.3s;

	&.-sm-padding {
		padding: 10px 0;
	}

	@include max-w(767) {
		padding: 10px 0;
	}

	&:hover {
		background-color: rgba($secondary, 0.05);
	}

	&__delete-col {
		width: 35px;
		display: -webkit-flex;
		display: -moz-flex;
		display: -ms-flex;
		display: -o-flex;
		display: flex;
		justify-content: center;

		@include max-w(767) {
			width: 25px;
		}

		@include max-w(575) {
			width: 50px;
		}
	}

	.cart-list-item {
		margin-bottom: 0;

		@include max-w(575) {
			margin-bottom: 10px;
		}
	}
}

.total-price-row {
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	align-items: center;
	justify-content: space-between;
	margin-bottom: 15px;

	&__title, &__price {
		margin-bottom: 0;
	}
}