.modal-close-btn {
	display: inline-block;
	font-size: 12px;
	line-height: 1;
	transform: scale(1);

	&__icon {
		display: inline-block;
		padding: 10px;
	}

	&:hover {
		transform: scale(1.2);
		background-color: rgba($secondary, 0.2);

		.modal-close-btn__icon {
			color: #fff;
			transition: 0.3s;
		}
	}

	&.-absolute-top-right-corner {
		position: absolute;
		z-index: 1;
	}

	&.-tr-10 {
		top: 10px;
		right: 10px;
	}

	&.-tr-25 {
		top: 25px;
		right: 25px;

		@include max-w(575) {
			top: 10px;
			right: 10px;
		}
	}

	&.-tl-25 {
		top: 25px;
		left: 25px;

		@include max-w(575) {
			top: 10px;
			left: 10px;
		}
	}
}