.stars-rating {

	&__current {
		margin-right: 8px;
		margin-bottom: 0;
		color: $gray;
	}

	.br-theme-fontawesome-stars-o {
		line-height: 1;
		margin-bottom: 2px;

		.br-widget {
			height: auto;

			a {
				font-family: 'icomoon' !important;
				font-size: 0.75rem;
				margin-left: 5px;
				margin-right: 0;

				&:first-child {
					margin-left: 0;
				}

				&:after {
					content: "\e903";
					color: $black;
				}
			}

			a.br-selected, a.br-active {
				&:after {
					content: '\e904';
					color: $black;
				}
			}
		}
	}

	&.-lg {
		.stars-rating__current {
			margin-right: 15px;
		}

		.br-theme-fontawesome-stars-o {
			.br-widget {
				a {
					font-size: 1.3rem;
					margin-left: 8px;

					@include max-w(991) {
						font-size: 1rem;
					}

					&:first-child {
						margin-left: 0;
					}
				}
			}
		}
	}
}