.side-menu {
	position: fixed;
	top: 0;
	left: 0;
	bottom: 0;
	z-index: 1100;
	width: 390px;
	background-color: $white;
	overflow-x: auto;
	transform: translate(-100%, 0);
	transition: 0.7s;

	&__container {
		padding: 45px 0 25px 0;
		height: calc(100% - 80px);
		display: -webkit-flex;
		display: -moz-flex;
		display: -ms-flex;
		display: -o-flex;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		overflow-x: auto;

		@include max-w(767) {
			height: calc(100% - 65px);
			padding-top: 25px;
		}

		@include max-w(575) {
			height: calc(100% - 55px);
			padding-bottom: 15px;
		}
	}

	&__list {
		margin-bottom: 50px;
	}

	// list animation
	.vertical-list {
		a {
			opacity: 0;
			transform: translate(-100px, 0);
			transition:transform 0.5s, text-shadow 0.3s, opacity 0.5s;
		}

		&.-animation-active {
			display: block;

			li {
				@for $i from 0 to 20 {
					$speed : 0.2s;
					$step : 0.05s * $i;

					$speed : $speed + $step;

					&:nth-child(#{$i + 1}) {
						a {
							transition-delay: $speed, 0s, $speed;
						}
					}
				}
			}

			a {
				opacity: 1;
				transform: translate(0, 0);
			}
		}
	}
	// list animation END
}

// OPEN SIDE MENU
body.-open-side-menu {
	overflow: hidden;

	.side-menu {
		transform: translate(0, 0);
	}
}

.side-menu-header {
	&__left {
		display: -webkit-flex;
		display: -moz-flex;
		display: -ms-flex;
		display: -o-flex;
		display: flex;
		align-items: center;
	}

	&__right {
		padding-right: 20px;

		@include max-w(575) {
			padding-right: 15px;
		}
	}
}

.catalog-list-item {
	&.-with-transfer {
		white-space: nowrap;

		&:hover {
			.catalog-list-item__icon {
				padding-left: 15px;
			}
		}

		&.-active {
			display: block;
			text-shadow: 0.3px 0 0 $body-color, -0.3px 0 0 $body-color, 0 0.3px 0 $body-color, 0 -0.3px 0 $body-color;

			.catalog-list-item__icon {
				padding-left: 15px;
			}
		}
	}

	&.-with-label {
		white-space: nowrap;
		position: relative;

		.catalog-list-item__label-wrap {
			position: absolute;
			top: -3px;
			left: calc(100% + 5px);
			white-space: nowrap;
			font-size: 0;
		}

		.catalog-list-item__label {
			font-size: 0.625rem;
			padding-right: 3px;
			text-shadow: none;

			&.-green {
				color: $green;
			}

			&.-red {
				color: $red;
			}
		}

		&:hover {
			.catalog-list-item__label {
				text-shadow: none;
			}
		}
	}

	&__icon {
		padding-left: 5px;
		transition-duration: 0.3s;
		transtion-delay: 0s;
		transition-property: padding-left;
		position: relative;
		top: 1px;
		font-size: 12px;
	}
}

.catalog-list {
	height: 100%;
	padding: 0 15px;

	@include max-w(575) {
		padding: 0 10px;
	}

	&.-first-col {
		padding-left: 0;
		padding-right: 10px;
	}

	&.-animation-active {
		display: block;
		&.-absolute {
			z-index: 10;
		}
	}

	&.-absolute {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		width: 100%;
		height: 100%;
		z-index: 1;
		display: none;
	}
}

.catalog-block {
	&__transfer {
		width: 100%;
		height: 420px;
		overflow-y: auto;
		position: relative;

		&.-border-left {
			border-left: 1px solid rgba($black, 0.1);
		}
	}
}