// CUSTOM ARROWS

.slider-animation-wrap {
	position: relative;
	.slider-arrows-offset {
		position: absolute;
		top: 0;
		left: 100%;
	}

	@include max-w(1300) {
		.slider-arrows {
			&.slider-arrows-offset {
				position: static;
				margin-left: 1px;
			}

			.slider-arrows-wrap {
				margin-bottom: 0px;
			}

			.slider-arrows-and-link-container {
				display: -webkit-flex;
				display: -moz-flex;
				display: -ms-flex;
				display: -o-flex;
				display: flex;
				align-items: center;
			}

			.square-buttons-vertical {
				font-size: 0;
				display: -webkit-flex;
				display: -moz-flex;
				display: -ms-flex;
				display: -o-flex;
				display: flex;
				margin-right: 20px;
				min-width: 71px;

				.btn-square {
					border-right: 1px solid $white;
					border-bottom: none;

					&:first-child {
						order: 2;
						border-right: none;
					}

					&:last-child {
						order: 1;
					}
				}
			}

			.link-underline-vertical {
				transform: none;
				margin-left: 0;
				writing-mode: inherit;

				&:before {
					top: auto;
					bottom: 0;
					left: 0;
					right: 0;
					width: 100%;
					height: 1px;
				}

				&:hover {
					&:before {
						width: 0;
						height: 1px;
					}
				}
			}
		}
	}
}

.slider-arrows {
	overflow: hidden;
	.slider-arrows-wrap {
		margin-bottom: 20px;
	}

	&--without-text {
		margin-bottom: 0;

		.slider-arrows-wrap {
			margin-bottom: 0;
		}
	}

	.link-underline-vertical {
		margin-left: 9px;
	}
}