// MAP MARKER
@mixin circle($circleSize){
	width: $circleSize;
	height: $circleSize;
	border-radius: $circleSize/2;
}

.html-marker {
	@include circle(24px);
	position: relative;
	border-radius: 50%;
	&:before,
	&:after {
		content: '';
		@include circle(24px);
		background-color: #303030;
		position: absolute;
		top: -6px;
		left: -6px;
		transform: scale(0.5);
		transform-origin: center center;
		animation: pulse-me 3s linear infinite;
	}
	&:after {
		animation-delay: 1.5s;
	}
}

@keyframes pulse-me {
	0%{
		transform: scale(0.5);
		opacity: 0;
	}
	50%{
		opacity: 0.25;
	}
	70%{
		opacity: 0.09;
	}
	100%{
		transform: scale(3.5);
		opacity: 0;
	}
}

// MAP STYLES
.map-wrap {
	position: relative;
	width: 100%;
	overflow: inherit;
	min-height: 300px;

	#map {
		position: absolute;
		top: 0;
		left: 0;
		bottom: 0;
	}

	// DO NOT SHOW LABEL ON GOOGLE MAP
	a[href^="http://maps.google.com/maps"]{display:none !important}
	a[href^="https://maps.google.com/maps"]{display:none !important}

	.gmnoprint a, .gmnoprint span, .gm-style-cc {
		display: none;
	}
	.gmnoprint div {
		background: none !important;
	}
}

@media only screen and (max-width: 575px) {
	.map-wrap {
		height: 200px;
		#map {
			right: 0 !important;
			bottom: 0 !important;
			height: 100%;
		}
	}
}