// keep aspect ratio of image
.img-wrap {
	position: relative;
	background-color: $black;

	&.-ratio-16-9 {
		padding-bottom: 56.25%;
	}

	&.-ratio-4-3 {
		padding-bottom: 75%;
	}

	&.-ratio-1-1 {
		padding-bottom: 100%;
	}

	& > img {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
	}
}

// responsive background image
.-resp-bg-img {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	@include rbg;
}

.square-block {
	position: relative;

	&:after {
		content: "";
		display: block;
		padding-bottom: 100%;
	}

	.square-content {
		position: absolute;
		width: 100%;
		height: 100%;
	}
}

.no-scroll {
	overflow-y: hidden;
}

.-overflow-hidden {
	overflow: hidden;
}

.absolute-img {
	position: relative;

	img {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
}

.custom-object-fit {
	background-size: cover;
	background-position: center;
	background-repeat: no-repeat;

	img {
		opacity: 0;
	}
}

.-mb-on-mob-15 {
	@include max-w(767) {
		margin-bottom: 15px;
	}
}

.-pl-1 {
	padding-left: 1px;
}

.-list-side-protection {
	padding-right: 15px;
}

.-faded-text {
	opacity: 0.5;
}

.doubled-list {
	> li {
		display: -webkit-flex;
		display: -moz-flex;
		display: -ms-flex;
		display: -o-flex;
		display: flex;
	}

	&__left, &__right {
		width: 50%;
		padding-right: 15px;

		@include max-w(575) {
			width: auto;
		}
	}

	&.-left-column-indent {
		.doubled-list__left {
			padding-left: 35px;

			@include max-w(991) {
				padding-left: 0;
			}
		}
	}
}