.btn {
	display: inline-block;
	text-decoration: none;
	border-radius: 0;
	text-align: center;
	position: relative;
	outline: none;
	transition: .35s;

	&:hover {
		text-decoration: none;
	}

	&-secondary {
		&:hover {
			color: $white;
			background-color: rgba($secondary, 0.8);
			border-color: transparent;
		}
	}

	&-md {
		min-width: 140px;
		padding: 0 5px;
		line-height: 36px;

		@include max-w(575) {
			min-width: 120px;
			line-height: 31px;
		}
	}

	&-md-wide {
		min-width: 200px;
		padding: 0 5px;
		line-height: 36px;

		@include max-w(575) {
			line-height: 31px;
		}
	}

	&-full-width {
		min-width: auto;
		width: 100%;
	}
}

.link-underline {
	display: inline-block;
	text-decoration: none;
	outline: none;
	transition: .35s;
	margin-bottom: 0.625rem;

	&-sm {
		position: relative;
		font-size: 0.875rem;
		line-height: 1.5;

		&:before {
			content: '';
			display: inline-block;
			position: absolute;
			bottom: 0;
			left: 0;
			right: 0;
			width: 100%;
			height: 1px;
			z-index: 1;
			background-color: $body-color;
			transition: all 0.3s ease-in-out;
		}

		&:hover {
			&:before {
				width: 0;
			}
		}
	}

	&-vertical {
		writing-mode: vertical-rl;
		transform: rotate(180deg);
		margin-bottom: 0;

		&:before {
			top: 0;
			bottom: 0;
			left: 0;
			right: auto;
			height: 100%;
			width: 1px;
		}

		&:hover {
			&:before {
				height: 0;
				width: 1px;
			}
		}
	}
}

.btn-square {
	transition: 0.3s;
	display: inline-block;
	position: relative;
	box-sizing: content-box;
	cursor: pointer;

	span {
		color: $white;
		font-size: 18px;
		display: inline-block;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
	}

	&-md {
		width: 40px;
		height: 40px;

		@include max-w(575) {
			min-width: 35px;
			width: 35px;
			height: 35px;
		}
	}

	&-lg-icon {
		span {
			font-size: 25px;

			@include max-w(575) {
				font-size: 16px;
			}
		}
	}

	&-almost-transparent {
		background-color: rgba($secondary, 0.1);
		&:hover {
			background-color: rgba($secondary, 0.5);
		}
	}

	&-gray {
		background-color: rgba($secondary, 0.2);

		&:hover {
			background-color: rgba($secondary, 0.5);
		}
	}

	&-secondary {
		background-color: $secondary;

		&:hover {
			background-color: rgba($secondary, 0.7);
		}
	}

	&-green {
		background-color: $green;
		&:hover {
			background-color: rgba($green, 0.7);
		}
	}

	&-red {
		background-color: $red;
		&:hover {
			background-color: rgba($red, 0.7);
		}
	}

	&-text {
		span {
			font-size: 0.625rem;
			font-family: 'Montserrat Alternates', sans-serif;
			font-weight: 200;
			color: $white;
			line-height: 1;
		}
	}
}

.square-buttons-vertical {
	.btn-square {
		display: block;
		margin-bottom: 1px;

		&:last-child {
			margin-bottom: 0;
		}
	}
}

.square-buttons-horizontal {
	font-size: 0;
	.btn-square {
		margin-right: 1px;
		&:last-child {
			margin-right: 0;
		}
	}
}

.gray-link {
	color: $gray;
	font-weight: 400;

	&:hover {
		color: $body-color;
	}
}

.tag-link {
	display: inline-block;
	background-color: #fff;
	padding: 0 8px;
	line-height: 22px;
	border: 1px solid $secondary;

	&:hover {
		background-color: $secondary;
		color: $white;
	}
}

.navigation-link {
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	align-items: center;

	&.-prev {
		.navigation-link__icon {
			margin-right: 8px;
		}
	}

	&.-next {
		.navigation-link__icon {
			margin-left: 8px;
		}
	}

	&__icon {
		font-size: 1.2rem;
	}
}

.left-paired-button {
	padding-right: 1px;
}

.right-paired-button {
	padding-left: 1px;
}