.products-filter-block {
	padding-right: 30px;
}

.filter-item {
	position: relative;
	margin-bottom: 25px;

	&__close {
		position: absolute;
		top: 0;
		right: 0;
	}

	&__title {
		margin-bottom: 20px;
	}

	&__full-width-block {
		margin-right: -30px;
	}
}

// range slider styles
.range-slider {
	max-width: 320px;
	height: 2px;
	border: none;
	background-color: $secondary;
	padding-left: 3px;
	padding-right: 17px;
	border-radius: 0;
	box-shadow: none;

	.noUi-handle {
		width: 20px;
		height: 20px;
		border-radius: 0;
		border: none;
		background-color: $secondary;
		box-shadow: none;
		cursor: pointer;

		&:focus {
			outline: none;
		}

		&:before, &:after {
			display: none;
		}
	}

	&.noUi-horizontal {
		.noUi-handle {
			top: -8px;
		}
	}
}

.range-slider-wrap {
	padding-top: 8px;
	padding-bottom: 10px;
}